/** some useful things, come from Bootstrap. Created by Dio Zhu. on 2016.9.23 */
.v-toast {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  /*width: 100%;*/
  max-width: 80%;
  position: fixed;
  padding: 0.4rem;
  border-radius: 0.13333rem;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  z-index: 2999;
  transition: opacity .3s linear;
}
.v-toast.is-placebottom {
    bottom: 1.33333rem;
    left: 50%;
    -webkit-transform: translate3d(-50%, 0, 0);
    transform: translate3d(-50%, 0, 0);
}
.v-toast.is-placemiddle {
    left: 50%;
    top: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}
.v-toast.is-placetop {
    top: 1.33333rem;
    left: 50%;
    -webkit-transform: translate3d(-50%, 0, 0);
    transform: translate3d(-50%, 0, 0);
}
/* autoprefixer: off */
.v-toast {
  z-index: 2999;
}
.v-toast-icon {
  display: block;
  text-align: center;
  font-size: 1.33333rem;
  margin: 0 0.53333rem;
}
.v-toast-text {
  font-size: 0.37333rem;
  line-height: 1;
  display: block;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
}
.v-toast-pop-enter, .v-toast-pop-leave-active {
  opacity: 0;
}


