/**
 * 加下面这两段样式是为了配合v-scroll、v-refresh组件，在被引入的页面进行滚动容器的设定，其他页面还原基本样式。
 * 目的是为了让输入页面input获取焦点时，键盘弹出的状态，使得页面滚动容器的滚动条不冲突。
 * QA在物业通-报修项目中新增报修时发现input获取焦点后上下滚动卡顿，实是滚动容器在输入法键盘弹出后出现了两个滚动条；
 * 另一个目的，如果滚动容器为默认的window的话，input获取焦点后，会把input顶上去，如果使用非window的滚动容器则不行；
 *              -- Author by Dio Zhu. on 2017.8.8
 */
/* loading */
.loading-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
  background: #FFF;
  opacity: 0.8;
}
.loading-wrapper.show {
    display: block;
}
.loading-wrapper.hide {
    display: none;
}
.loading-wrapper .loading-img {
    position: absolute;
    width: 0.8rem;
    height: 0.8rem;
    top: 45%;
    left: 50%;
    margin-top: -0.4rem;
    margin-left: -0.4rem;
    text-align: center;
}
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.fade-enter-active {
  -webkit-transition: opacity .3s ease;
  -o-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
